html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}
.h1,
.h2,
.h3,
.h4,
body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
}
@font-face {
  font-family: 'UTM Androgyne';
  src: url('fonts/utm-androgyne.ttf') format('truetype');
}
html,
body {
  height: 100%;
  overflow: scroll;
  font-family: 'UTM Androgyne';
}
.navbar {
  margin-bottom: 0;
}
.navbar .navbar-brand,
.navbar .navbar-nav li a {
  line-height: 80px;
  height: 80px;
  padding-top: 0;
}
.index-page {
  margin-left: -15px;
  margin-right: -15px;
  padding-left: 8%;
  padding-right: 8%;
  padding-top: 80px;
  position: relative;
}
.index-page .border {
  position: relative;
}
.index-page .centered {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  display: inline-block;
}
.index-page .section-header {
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  height: 80px;
  text-align: center;
  vertical-align: middle;
}
.index-page .section-header h1,
.index-page .section-header h2,
.index-page .section-header h3,
.index-page .section-header h4,
.index-page .section-header h5,
.index-page .section-header h6 {
  line-height: 80px;
}
.index-page#main {
  height: 100%;
  background: url('images/home/intro.png');
  -webkit-background-size: cover;
  background-size: cover;
  text-align: center;
}
.index-page#main h3 {
  font-size: 2.4em;
  padding-top: 0.3em;
  padding-bottom: 0.3em;
}
.index-page#main .btn {
  font-size: 1.8em;
}
.index-page#top-articles .item {
  height: 400px;
  background-color: palegreen;
}
.index-page#top-destinations .item img.avatar {
  height: 250px;
  width: 100%;
}
.index-page#top-destinations .item div.content {
  /*position: absolute;
        top: 1em;
        left: 1em;
        right: 1em;
        padding: 1em;*/
  text-align: center;
  color: white !important;
}
